:root {
  --moderate-cyan: hsl(176, 50%, 47%);
  --dark-cyan: hsl(176, 72%, 28%);
  --black: hsl(0, 0%, 0%);
  --dark-gray: hsl(0, 0%, 48%);
  --gray: hsl(0, 0%, 90%);
  --light-gray: hsl(0, 0%, 95%);
}

:root {
  --font-commissioner: "Commissioner", sans-serif;
  --font-bold: 700;
  --font-regular: 500;
  --font-light: 400;
}

body {
  font-family: var(--font-commissioner);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-commissioner);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h2 {
  margin-bottom: 0;
}

p {
  margin-top: 5px;
  opacity: 50%;
  font-weight: var(--font-regular);
  font-size: 1.1rem;
  line-height: 1.9rem;
}

button {
  border: none;
  background-color: var(--moderate-cyan);
  border-radius: 50px;
  padding: 20px 30px;
}

nav {
  height: 550px;
  background: url(../../../images/image-hero-desktop.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  padding-left: 200px;
  padding-right: 200px;
  padding-top: 50px;
}
nav .nav__container {
  display: flex;
  justify-content: space-between;
}
nav .nav__container ul {
  display: flex;
  list-style: none;
}
nav .nav__container ul li a {
  color: white;
  margin-left: 2rem;
}

.starter {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
  padding: 50px 60px;
  text-align: center;
}
.starter .logo {
  position: absolute;
  top: -28px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}
.starter h2 {
  font-size: 2.25rem;
  color: var(--black);
}
.starter p {
  margin-bottom: 40px;
  letter-spacing: 0.05rem;
}
.starter div {
  display: flex;
  justify-content: space-between;
}
.starter .button {
  height: 56px;
  font-weight: var(--font-bold);
  font-size: 1.1rem;
  letter-spacing: 0.05rem;
  color: white;
  cursor: pointer;
  transition: background 100ms ease-in-out;
}
.starter .button:hover {
  background-color: var(--dark-cyan);
}
.starter .bookmark {
  height: 56px;
  padding-left: 0;
  padding-right: 25px;
  text-align: center;
  background-color: var(--gray);
  display: flex;
  align-items: center;
  font-weight: var(--font-bold);
  font-size: 1.1rem;
  color: var(--dark-gray);
  cursor: pointer;
  transition: opacity 100ms ease-in-out;
}
.starter .bookmark img {
  margin-right: 10px;
}
.starter .bookmark:hover {
  opacity: 75%;
}

.stats {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
  padding: 50px 60px;
}

.stats .all_stats_container {
  display: flex;
}
.stats .all_stats_container .stats_container:nth-child(1) {
  padding-right: 80px;
}
.stats .all_stats_container .stats_container:nth-child(2), .stats .all_stats_container .stats_container:nth-child(3) {
  padding-inline: 80px;
}
.stats .all_stats_container .stats_container:nth-child(1), .stats .all_stats_container .stats_container:nth-child(2) {
  border-right: 1px solid var(--gray);
}
.stats .all_stats_container .stats_container h2 {
  font-size: 2.25rem;
  color: var(--black);
}
.stats .all_stats_container .stats_container p {
  letter-spacing: 0;
}
.stats .stats-bar {
  width: auto;
  height: 10px;
  margin-top: 30px;
  background-color: var(--gray);
  border-radius: 50px;
}
.stats .stats-bar .inner-bar {
  width: 80%;
  height: inherit;
  background-color: var(--moderate-cyan);
  border-radius: 50px;
}

.about {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
  padding: 50px 60px;
}
.about h3 {
  font-size: 1.3rem;
  font-weight: var(--font-bold);
  margin-bottom: 40px;
}
.about p {
  margin-bottom: 35px;
  letter-spacing: 0.025rem;
}
.about .card {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
  padding: 50px 60px;
}
.about .card .headline {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.about .card .headline h3,
.about .card .headline p {
  margin-bottom: 25px;
  padding: 0;
}
.about .card .headline h3 {
  font-size: 1.3rem;
  font-weight: var(--font-bold);
}
.about .card .headline p {
  color: var(--moderate-cyan);
  opacity: 100%;
}
.about .card .footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.about .card .footer .p-container {
  display: flex;
  align-items: center;
}
.about .card .footer .p-container .numbers-left {
  margin: 0;
  margin-right: 7px;
  font-size: 2rem;
  color: var(--black);
  opacity: 100%;
  font-weight: var(--font-bold);
}
.about .card .footer .p-container .smaller {
  margin: 0;
  font-size: 1rem;
}
.about .card .footer .button-smaller {
  padding-inline: 40px;
  font-weight: var(--font-bold);
  font-size: 1rem;
  color: white;
  cursor: pointer;
  transition: background 100ms ease-in-out;
}
.about .card .footer .button-smaller:hover {
  background: var(--dark-cyan);
}
.about .last {
  opacity: 50%;
}
.about .last .button-gray {
  background-color: var(--dark-gray);
  padding-inline: 40px;
  font-weight: var(--font-bold);
  font-size: 1rem;
  color: white;
}

body {
  background-color: var(--light-gray);
}

main {
  position: absolute;
  top: 425px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  max-width: 925px;
}